}

.header {
  border: var(--border-dimensions) var(--border-style) var(--border-color);
  border-radius: var(--border-radius);
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
  overflow: hidden;
}

.heading__primary {
  letter-spacing: -2px;
  text-align: center;
  border-bottom: var(--border-dimensions) var(--border-style) var(--border-color);
  font-size: 6.4rem;
  font-weight: 500;
}

.date {
  letter-spacing: -.5px;
  text-align: center;
  margin-top: 1.2rem;
  margin-bottom: .4rem;
  font-size: 3.2rem;
  font-weight: 500;
}

.time {
  text-align: center;
  letter-spacing: -.4px;
  margin-bottom: 1.2rem;
  font-size: 2.4rem;
}

.to-do-list {
  height: 100%;
  border: var(--border-dimensions) var(--border-style) var(--border-color);
  background-color: var(--to-do-bg-color);
  height: 55rem;
  border-top: none;
  flex-direction: column;
  align-items: center;
  gap: 2.4rem;
