  height: 100%;
  width: 100%;
  flex-direction: column;
  gap: 1.2rem;
  padding: 0 1.2rem 1.2rem;
  display: flex;
}

.todo__elem {
  border-bottom: 1px solid #fff3bf;
  justify-content: space-between;
  align-items: center;
  gap: 2.4rem;
  padding-bottom: 1.2rem;
  list-style-type: none;
  display: flex;
  position: relative;
}

.todo__elem_box {
  justify-content: center;
